.register-container {
    max-width: 500px;
    margin: 2rem auto;
    padding: 2rem;
    border: 1px solid #ccc;
    border-radius: 12px;
    background-color: #fff;
    font-family: sans-serif;
  }
  
  .register-title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2rem;
  }
  
  .register-form label {
    display: block;
    margin-bottom: 1.5rem;
    font-weight: bold;
  }
  
  .input-group {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 6px;
    padding: 0.5rem;
    margin-top: 0.5rem;
  }
  
  .input-group .icon {
    margin-right: 0.5rem;
    font-size: 1.2rem;
  }
  
  .input-group input,
  .input-group textarea {
    flex: 1;
    border: none;
    outline: none;
    font-size: 1rem;
    resize: none;
  }
  
  .submit-button {
    display: block;
    width: 100%;
    padding: 0.75rem;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    margin-top: 1rem;
  }
  
  .submit-button:hover {
    background-color: #555;
  }
  
  .back-button {
    margin-top: 1.5rem;
    background: none;
    border: none;
    color: #007bff;
    cursor: pointer;
    font-size: 0.95rem;
    text-decoration: underline;
  }
  