.nav-bar {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
  }
  
  .nav-button {
    padding: 0.5rem 1rem;
    border: 2px solid #999;
    border-radius: 5px;
    background-color: white;
    cursor: pointer;
  }
  
  .nav-button.active {
    background-color: #a9cfff;
    font-weight: bold;
  }
  
  
  .nav-button:hover {
    background-color: #333;
    border-radius: 5px;
  }
  
 